@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

$color-primary: #00A69C;
$color-secondary: #F05A28;
$main-spacing: 120px;

html {
    scroll-behavior: smooth;
}

body {
    padding: 0;
    margin: 0;
    font-family: "Roboto", sans-serif;
    line-height: 1.5;

    section {

        .container {
            width: 70%;
            margin: 0 auto;
        }

        //Banner Styling begins here...
        .banner_parent {
            .banner {
                width: 100%;
                height: 100dvh;
                background: url(../images/background.webp);
                background-size: 50%;
                background-position: center;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                .overlay{
                    width: 100%;
                    height: 100dvh;
                    background: rgba(0, 0, 0, 0.097);
                    position: absolute;
                    top: 0;
                    z-index: -1;
                }

                .banner_content {
                    width: max(300px, 35%);
                    height: max(400px,75vh);
                    background-color: white;
                    //padding: max(10px,3%) 20px;
                    border-radius: 30px;
                    box-shadow: 0px 0px 50px rgb(177, 177, 177);
                    display: grid;
                    place-items: center;

                    .content {
                        width: 60%;
                        margin: auto;
                        text-align: center;
                        display: flex;
                        flex-direction: column;
                        align-items: center;

                        .logo {
                            width: 20%;

                            img {
                                width: 100%;
                            }
                        }

                        h1 {
                            font-size: clamp(.8rem, 3vw, 4rem);
                            width: 300px;
                            font-weight: 400;
                            color: $color-secondary;
                            margin: 0;
                            line-height: 1.2;
                        }

                        h3 {
                            font-size: clamp(.8rem, 2vw, 2rem);
                            margin-bottom: 0;
                            color: darkslategrey;
                            font-weight: 300;
                        }

                        h4 {
                            margin: 5px 0px;
                            font-weight: 300;
                            font-size: clamp(1rem, 1vw, 1.5rem);
                            color: darkslategrey;
                            margin-bottom: max(20px, 3%);
                            //color: rgb(86, 86, 86);
                        }

                        p {
                            font-size: clamp(.8rem, .8rem, 1rem);
                        }

                        a{
                            width: 100%;
                            cursor: pointer;
                            button {
                                width: 100%;
                                padding: 20px 0;
                                font-size: clamp(1rem, 1.2vw, 1.2rem);
                                background-color: $color-secondary;
                                color: white;
                                border: none;
                                border-radius: 15px;
                                margin-top: 20px;
                                cursor: pointer;
                            }
                        }

                        
                    }
                }

                .down_arrow {
                    background-color: $color-secondary;
                    padding: 10px;
                    border-radius: 50%;
                    margin-top: 30px;
                    width: 35px;
                    height: 35px;
                    display: grid;
                    place-items: center;
                    cursor: pointer;

                    img {
                        width: 70%;
                    }
                }
            }

        }

        //Programe outline styling begins here...
        .program_parent {
            margin-top: $main-spacing;
            background: url(../images/pattern_1.png);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: right;

            .container {
                width: 70%;
                margin: auto;

                h1 {
                    width: 100%;
                    text-align: center;
                    font-size: clamp(1rem, 2.5vw, 2.5rem);
                    font-weight: 400;
                    color: $color-secondary;
                    margin-bottom: 50px;
                }

                .program_container {
                    border: 3px solid $color-primary;
                    border-radius: 30px;
                    overflow: hidden;

                    .header {
                        padding: 30px;
                        background-color: $color-primary;
                        color: white;
                        font-size: clamp(1rem, 2vw, 2rem);
                    }

                    .program_body {
                        padding: 50px 50px 100px 50px;
                        background-color: rgba(255, 255, 255, 0.811);

                        .program_calender {
                            .program_flexbox {
                                display: flex;

                                .flex {
                                    padding: 10px;

                                    .mobile_time{
                                        opacity: 0;
                                        color: $color-secondary;
                                    }

                                    p {
                                        margin: 0;
                                        line-height: 1.5;
                                    }

                                    .placeholder {
                                        opacity: 0;
                                    }

                                    .dates {
                                        color: $color-secondary;
                                    }

                                    &:nth-child(1) {
                                        color: $color-secondary;
                                    }

                                    &:nth-child(2) {
                                        display: flex;
                                        flex-direction: column;
                                        align-items: center;

                                        .circle {
                                            width: 15px;
                                            height: 15px;
                                            background-color: $color-secondary;
                                            border-radius: 50%;
                                        }

                                        .line {
                                            width: 1px;
                                            height: 80px;
                                            border-left: 1px solid $color-secondary;
                                            margin-top: 10px;
                                        }
                                    }

                                    &:nth-child(3) {
                                        flex-grow: 1;
                                    }

                                    &:nth-child(4) {
                                        font-size: clamp(.9rem, 2rem, 2rem);
                                        display: grid;
                                        place-items: center;
                                        color: $color-secondary;
                                        cursor: pointer;
                                    }
                                }
                            }
                        }
                    }


                }
            }
        }

        //Accomodation styling starts here...
        .accomodation_parent {
            margin-top: $main-spacing;
            background-image: url(../images/pattern_2.png);
            background-repeat: no-repeat;
            background-size: 30%;
            background-position: left center;


            .container {
                width: 70%;
                margin: 0 auto;

                .header {
                    text-align: center;


                    color: $color-secondary;

                    h1 {
                        font-weight: 400;
                        font-size: clamp(1rem, 2.5vw, 2.5rem);
                    }
                }

                .flexbox {
                    display: flex;

                    .flex {
                        width: 50%;

                        position: relative;

                        &:nth-child(2) {
                            display: flex;
                            align-items: center;
                            justify-content: flex-end;

                            img {
                                width: 50%;
                                position: absolute;
                            }
                        }

                        h2 {
                            color: $color-primary;
                            font-weight: 400;
                            margin-bottom: 40px;
                        }
                        

                        button {
                            background-color: $color-secondary;
                            border: none;
                            padding: 20px 40px;
                            color: white;
                            font-size: clamp(1rem, 1vw, 1rem);
                            border-radius: 15px;
                            margin-top: 30px;
                            cursor: pointer;
                        }


                        .location {
                            display: flex;
                            align-items: center;

                            .icon {
                                width: 20px;


                                img {
                                    width: 100%;

                                }
                            }

                            p {
                                margin-left: 10px;
                            }
                        }



                    }
                }

                .flights_parent {
                    margin-top: $main-spacing;

                    .flight_content {
                        color: black;
                        text-align: left;

                        h2 {
                            font-weight: 400;
                            color: $color-primary;
                        }

                        .flight_flexbox {
                            display: flex;
                            align-items: center;

                            p {
                                margin-left: 30px;
                            }
                        }
                    }

                }
            }
        }

        //Visa Information styling begins here...
        .visa_parent {
            margin-top: $main-spacing;
            background-image: url(../images/pattern_3.png);
            background-size: contain;
            background-position: right;
            background-repeat: no-repeat;

            .container {
                .header {
                    text-align: center;

                    h1 {
                        font-weight: 400;
                        font-size: clamp(1rem, 2.5vw, 2.5rem);
                        color: $color-secondary;
                    }
                }

                .button_holder {
                    padding: 30px 0;
                    border-bottom: solid 1px $color-primary;

                    button {
                        border: none;
                        border: 1px solid $color-primary;
                        color: $color-primary;
                        padding: 20px 40px;
                        background-color: white;
                        border-radius: 30px;
                        font-size: clamp(1rem, 1.2vw, 1.2rem);

                        &:nth-child(2) {
                            margin-left: 30px;
                        }
                    }


                }

                .details {
                    p {
                        font-weight: bold;
                    }

                    ol {
                        line-height: 2;
                    }
                }
            }
        }

        //Speakers styling begins here...
        .speakers_parent {
            margin-top: $main-spacing;
            

            .container {
                .header {
                    text-align: center;

                    h1 {
                        font-weight: 400;
                        font-size: clamp(1rem, 2.5vw, 2.5rem);
                        color: $color-secondary;
                    }
                }

                .flexbox {
                    display: flex;
                    justify-content: space-between;
                    gap: 20px;

                    .speaker {
                        text-align: center;
                        width: 25%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        //padding: 10px;

                        .speaker_img {
                            width: 100%;
                            aspect-ratio: auto 1/1;
                            border: 2px solid $color-primary;
                            border-radius: 20px;
                            background-image: url(https://img.freepik.com/free-photo/portrait-young-man-isolated_23-2149158608.jpg?t=st=1716281778~exp=1716285378~hmac=6d818f18bc8cefeeaeff4b91e8d6eed21846401534476e5ef7cb50e7f898fcc1&w=740);
                            background-size: cover;
                        }

                        .details {
                            margin: 20px 0;

                            h3,
                            p {
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }

        //Footer styling begins here...
        footer {
            margin-top: $main-spacing;

            .complete_registration {
                width: 100%;
                height: 200px;
                background-color: rgba(233, 233, 233, 0.527);
                display: grid;
                place-items: center;

                button {
                    background-color: $color-secondary;
                    padding: 20px 80px;
                    color: white;
                    font-size: clamp(1rem, 1.5vw, 1.5rem);
                    border: none;
                    border-radius: 10px;
                    cursor: pointer;
                }
            }

            .footer_parent {
                background-color: $color-primary;
                padding: 80px 0;

                .container {
                    .flexbox {
                        display: flex;
                        align-items: center;
                        gap: 20px;

                        color: white;

                        .flex {
                            .logo {

                                background-color: white;
                                position: relative;
                                border-radius: 50%;
                                width: 150px;
                                height: 150px;

                                display: grid;
                                place-items: center;

                                img {
                                    width: 60%;
                                    position: absolute;


                                }

                            }

                            &:nth-child(2){
                                p{
                                    width: 60%;
                                    font-size: .9rem;
                                }
                            }

                            &:nth-child(3){
                                width: 70%;

                                .socials{
                                    border-bottom: 2px solid white;
                                    ul{
                                        list-style-type: none;
                                        padding-left: 0;
                                        li{
                                            display: inline-block;
                                            padding-left: 20px;
                                            width: 10%;
                                            aspect-ratio: auto 1/1;

                                            img{
                                                width: 100%;
                                            }
    
                                            &:nth-child(1){
                                                padding-left: 0;
                                            }
                                        }
                                    }
                                }

                                
                            }

                        }
                    }
                }

                
            }

            .footer_pattern{
                width: 100%;
                height: 50px;
                background-image: url(../images/background.png);
            }
        }
    }
}